
export default {
  layout: 'empty',
  async asyncData ({ $content, params, redirect }) {
    const articles = await $content('blog')
      .where({
        wip: false
      })
      .sortBy('creationDate', 'desc')
      .only(['title', 'coverImage', 'tags', 'creationDate', 'slug'])
      .fetch()

    const tagDefs = await $content('settings')
      .where({ type: 'blogTags' })
      .only(['tagEn', 'tagZh'])
      .fetch()

    return { articles, tagDefs }
  }
}
